import React from 'react';
import BookList from './BookList';
import axios from 'axios';
import ReadedBooks from './ReadedBooks';
import CurrentlyReading from './CurrentlyReading';
import WantToRead from './WantToRead';
import {Route , Link} from 'react-router-dom';





   
class App extends React.Component{
  
    state={
        allOfBooks:[],
        readedBooks:[],
        currentlyReading:[],
        wantToRead:[]
    };

    

  componentWillMount=()=>{      
      axios({
        method:'get',
        url:'https://api.nytimes.com/svc/books/v2/lists/2010-10-01/trade-fiction-paperback.json?list-name=hardcover-fiction&api-key=32ur3KWXvl87ZVz6s0OMMmDlvy42DXuZ',
        responseType:'json'
      })
      .then((response)=> { 
        console.log(this);
        this.setState({allOfBooks:response.data.results}); 
      })

      .catch((err)=> {
        console.log(err)
      });
      
      
  }
    
    addToReadedList =(book)=>{
      if (this.state.readedBooks.map((b)=>b.book_details[0].title !==  book.book_details[0].title)){
          this.setState((state)=>({   
          readedBooks: state.readedBooks.concat(book),
          wantToRead: state.wantToRead.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10),
          currentlyReading: state.currentlyReading.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)


             
        }));
      };


    }

    addToCurrentlyReadingList =(book)=>{
        this.setState((state)=>({
          currentlyReading: state.currentlyReading.concat(book),
          readedBooks: state.readedBooks.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10),
          wantToRead: state.wantToRead.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)
        }));
    }

    addToWantToReadList=(book)=>{
      this.setState((state)=>({
        
        wantToRead: state.wantToRead.concat(book),
        readedBooks: state.readedBooks.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10),
        currentlyReading: state.currentlyReading.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)

      }));
    }

    removeFromReadedList =(book)=>{
      this.setState((state)=>({
        readedBooks: state.readedBooks.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)
      }));
    }

    removeFromWantToReadedList =(book)=>{
      this.setState((state)=>({
        wantToRead: state.wantToRead.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)
      }));
    }

    removeFromCurrentlyReadingList =(book)=>{
      this.setState((state)=>({
        currentlyReading: state.currentlyReading.filter((b)=> b.book_details[0].primary_isbn10 !== book.book_details[0].primary_isbn10)
      }));
    }

    


    
    
    render(){   

      
    return (<div className="ui container ">

        
        <div className="content">

        <Route exact path = "/" render={()=>(
           <BookList 
           content={this.state.allOfBooks}
           onAddToReadedList={this.addToReadedList}
           onAddToWantToReadList={this.addToWantToReadList} 
           onAddToCurrentyReadingList={this.addToCurrentlyReadingList}
           />
        )}/>
        
        {/* <div className="ui divider"></div>

        <BookList 
        content={this.state.allOfBooks}
        onAddToReadedList={this.addToReadedList}
        onAddToWantToReadList={this.addToWantToReadList} 
        onAddToCurrentyReadingList={this.addToCurrentlyReadingList}
        /> */}
      
      <Route path= "/ShowCreatedList" render={()=>(     
        <>

        <Link 
        to="/"
        ><i className="angle left icon huge"></i>Search and Add to lists
        </Link>

        <ReadedBooks
        content={this.state.readedBooks} 
        onRemoveFromReadedList={this.removeFromReadedList}
        />

        <WantToRead
        content={this.state.wantToRead}
        onAddToReadedList={this.addToReadedList}
        onRemoveFromReadedList={this.removeFromWantToReadedList} 
        onAddToCurrentyReadingList={this.addToCurrentlyReadingList}
        />
        
        <CurrentlyReading
        content={this.state.currentlyReading}
        onAddToReadedList={this.addToReadedList}
        onAddToWantToReadList={this.addToWantToReadList} 
        onRemoveFromCurrentlyReadingList={this.removeFromCurrentlyReadingList}
        />

        <Link 
        to="/"
        ><i className="search icon big"></i>Search and Add to lists
        </Link>
        </>
        )}/>
              
        </div>
        </div>
    );
};
};

 


export default App; 