import React from 'react'; 


const CurrentlyReading  = (props) =>{
    let currentlyReadingList = [...new Set(props.content)];


    return(
        <div className="ui">
        <h2 className="center aligned header">Currently Reading Books List</h2>
        <div className="ui divider"></div>

                
                <div className="ui items cards grid">

                {props.content.map((book)=>(
                    <div key={book.book_details[0].primary_isbn10} className="ui item eight wide column">
                    <div className="ui card">
                            <div className="image">
                            <img src={book.book_details[0].book_image} alt="book"/>
                            </div>
                        <div className="content">
                        
                                <a className="header">{book.book_details[0].title}</a>
                                <div className="meta">
                                    <span>{book.book_details[0].author}</span>
                                </div>
                                <div className="description">
                                    <p>{book.book_details[0].description}</p>
                                </div>
                                <div className="extra">
                                    <span>$ {book.book_details[0].price}</span>
                                </div>

                                <div className="ui compact menu">
                                <div className="ui simple dropdown item">
                                    Options
                                    <i className="dropdown icon"></i>
                                    <div className="menu">
                                    <div className="item" onClick={(e) =>props.onRemoveFromCurrentlyReadingList(book)}>Remove From Currently Reading List</div>
                                    <div className="item" onClick={(e) =>props.onAddToWantToReadList(book)}>Add To Want To Read List</div>
                                    <div className="item" onClick={(e) =>props.onAddToReadedList(book)}>Add To Readed List</div>
                                    </div>
                                </div>
                                </div>
                        </div>
                    </div>
                    </div>
                ))}
                </div>
                <div className="ui divider"></div>

               
        </div>
    );
};

export default CurrentlyReading;



