import React from 'react'; 
import EscapeRegExp from 'escape-string-regexp'; 
import {Link} from 'react-router-dom';
// import SortBy from 'sort-by';



class BookList extends React.Component {

    state = {querry : ''};

    onInputChange=(querry)=>{
        this.setState({
            querry:querry
        })


    };
       
    render(){
        let bookList =this.props.content;

        let showingList ; 

        if(this.state.querry){
            const match = new RegExp(EscapeRegExp(this.state.querry),'i');

            showingList=bookList.filter((book)=>(match.test(book.book_details[0].title)));
        }
        else {
            showingList=bookList;
        }

        // showingList=showingList.slice(0,4);
        
        // showingList.book_details.sort(SortBy('title'));

        return (<>
                <div className="ui divider"></div>
                <div className="ui fluid icon input">
                
                                <input
                                type="text" 
                                placeholder="Search"
                                onChange={(e)=>this.onInputChange(e.target.value)}
                                value={this.state.querry}
                                
                                />
                                
                                
                                <Link
                                to="/ShowCreatedList"
                                className="ui button"
                                >
                                <i className=" book icon big"></i>Show Lists
                                </Link>
                                
                                
                            
                </div>

                <div className="ui divider"></div>
                <h2>Book List</h2>
                <div className="ui divider"></div>
        
        <div className="ui cards grid">
       

                   
                    {showingList.map((book)=>(
                        <div key={book.book_details[0].primary_isbn10} className="ui card seven wide column">
                        
                        <div className="">
                            <img  className="right floated mini ui image" alt="book" src={book.book_details[0].book_image}/>
                        </div>
                        
                        <div className="content">
                            <div>
                                <div className="header">{book.book_details[0].title}</div> 
                                <div className="meta">{book.book_details[0].author}</div>
                                <div className="description">{book.book_details[0].description}</div>
                                
                                <div className="ui compact menu">
                                <div className="ui simple dropdown item">
                                    Move to
                                    <i className="dropdown icon"></i>
                                    <div className="menu">
                                    <div className="item"onClick={() =>this.props.onAddToReadedList(book)}> Add To Readed List</div>
                                    <div className="item"onClick={() =>this.props.onAddToCurrentyReadingList(book)}> Add To Currently Readed List</div>
                                    <div className="item"onClick={() =>this.props.onAddToWantToReadList(book)}> Add TO Want to Read List</div>
                                    </div>
                                </div>
                                </div>
                                
                            </div>
                        </div>  
                        </div> 
                
                    ))}

                    <Link
                    to="/ShowCreatedList"
                    className="ui button"
                    >
                    <i className=" book icon big"></i>Show Lists
                    </Link>

        
            
            
        </div>
                        
        </>
        );
    }
}


export default BookList; 